.App {
  font-family: Arial, sans-serif;
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.company-logo {
  width: 100px;
  height: auto;
}

.navigation {
  list-style-type: none;
  padding: 0;
}

.navigation li {
  display: inline;
  margin: 0 15px;
}

.navigation li a {
  color: white;
  text-decoration: none;
}

footer {
  background-color: #282c34;
  color: white;
  padding: 10px 0;
}

footer .footer-links ul {
  list-style-type: none;
}

footer .footer-links li {
  display: inline;
  margin: 0 10px;
}

footer .footer-links li a {
  color: white;
  text-decoration: none;
}

